         entendu parler d'une nouvelle ide de produit : une appli 
         qui permettrait aux clients de transfrer de l'argent pour 
         grer leurs finances ou payer leurs amis. Pendant que vous 
         tiez en formation  lextrieur, le directeur de l'entreprise 
         a organis une runion pour mettre votre quipe au courant des 
         besoins produit de cette nouvelle appli. Votre quipe, compose 
