<script>
import commonMixin from "../mixin/commonMixin"
export default {
  name: "Homepage",
  mixins: [commonMixin],
  created: function() {
    
  },

  mounted: function() {

    this.$emit('pagetitle', "Who are we ?")
    this.loadPMBMoney()

  },


  computed: {
    
   
    
  },
