          console.log(response)
          self.pmbMoney = response.data
        })
        .catch(function(error) {
          console.log(error)
        })
        .finally(function() {

        });
    },
    
    
  },

  data () {

    return{

      drawer: false,
      group: null,
      pmbMoney: 0,
    }
  },
};
</script>

